.newProduct {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }

.newProduct__container{
    border-radius: 25px;
    padding:20px;
    margin-top:30px;
    margin-bottom:30px;
    width: 600px;
    background-color:rgba(255,255,255,0.2);
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    display: flex;
    justify-content:center;
}
.row{
    width: 100%;
}

.col__text{
    text-align:center;
    color: white;
}

.col__btn{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-self: center;
}
.col__dates{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 16px;
}
.col__dates>div{
    width: 48%;
}
.col__productManager{
    display:flex;
    flex-direction: row;
    justify-content:center;
    align-items: center;
    margin-bottom: 16px;
}

.col__productManager-select{
    display:flex;
    flex-direction: row;
    align-items: center;
}
.col__productManager-select>button{
    display:flex;
    justify-content:center;
    align-items: center;
    text-align: center;
    font-size:28px;
    color: white;
    margin-left: 10px;
    width:30px;
    height:30px;
    background-color: transparent;
    border-radius: 50%;
    border: solid 2.3px white;
}

.col__productManager-select>button:hover{
    background-color: rgba(255,255,255,0.5);
}


.Icon__newProduct {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height:35px;
    width:42.5px;
    margin-left: 5px;
    font-weight: bold;
    color:#fff;
  }
.Icon-add:hover{
    color: rgb(16, 87, 4);
}

@media only screen and (max-width: 600px) {

    .newProduct__container{
        width: 90%;
    }
    .col__productManager-select>select{
        flex-direction: column;

    }
  }

  @media only screen and (max-width: 380px) {

    .col__productManager-select>select{
        font-size: 0.8rem;
    }
  }