.register {
  background-image: linear-gradient(gray,#1C3643);
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 0px 30px;
  min-height: calc(100vh - 200px); /* El ancho será igual al tamaño de todo el height menos 200px (100px del header + 100px del footer) */
}

.register__container {
  background-color: rgba(255,255,255,0.1);
  border: 2px solid white;
  border-radius: 40px;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 60px 68px 40px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.register__container {
  text-align: center;
}

.register__container--form {
  display: flex;
  flex-direction: column;
}
.register__container--form >input::placeholder{
  color: white;
}


.register__container a {
  color: white;
  font-size: 16px;
  text-align: center;
  text-decoration: none;
}

.register__container a:hover {
  text-decoration: underline;
}

.register__container--form > .input {
  background-color: transparent;
  border-bottom: 2px solid white;
  border-left: 0px;
  border-right: 0px;
  border-top: 0px;
  color: white;
  font-family: 'Muli', sans-serif;
  font-size: 16px;
  height: 50px;
  margin-bottom: 20px;
  outline: none;
  padding: 0px 20px;
  border-radius: 25px;
}


.register__container--form > .button {
  background-color: rgba(255, 255, 255, 0.3);
  border: none;
  border-radius: 25px;
  color: white;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  font-family: 'Muli', sans-serif;
  height: 50px;
  letter-spacing: 1px;
  margin: 10px 0px;
}

.register__container--form > .button:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

@media only screen and (max-width: 600px) {

  .register__container {
    width: 320px;
    padding: 60px 30px 40px;

  }
}

@media only screen and (max-width: 300px) {

  .register__container {
    width: 250px;
    padding: 60px 30px 40px;
  }
}